
.overlayTopBar {
    position: sticky;
    top: 0%;
    z-index: 10000;
}

.overlayCard {
    min-height:50vh;
    min-width:25vw;
    margin-bottom:0 !important;
    padding-left: 2em;
    padding-bottom: 1em;
    padding-top: 50%;
    filter: grayscale(1);
    border-bottom: 5px solid lightgrey;
    max-width: 100vw;
}

.overlayCard hr {
    border: 1px dashed black;
    width: 97%;
}

.overlayCard h4 {
    font-size: 1.25em;
}

.overlayCard p {
    color: black;
    font-size: .9em;
}

.overlayCard i {
    font-size: 2em;
}

.overlayCard:first-child {
    background: lightgrey;
    /* background: url("https://picsum.photos/500/500?random=1") no-repeat; */
}

.overlayCard:nth-child(2) {
    background: lightsteelblue;
    /* background: url("https://picsum.photos/500/600?random=1") no-repeat;   */
}

.overlayCard:nth-child(3) {
    background: whitesmoke;
    /* background: url("https://picsum.photos/500/700?random=1") no-repeat; */
}

.overlayContainer {
    min-width: initial;
    min-height: initial;
    z-index: 10000;
    background: whitesmoke;
}

@media screen and (min-width: 992px) {

    .overlayContainer {
        min-width: 100%;
        min-height: 100vh;
        background: rgba(192,192,192,0.3);
    }

    .overlayCard {
      padding: 2em;
      min-height: initial;
      min-width: initial;
      border-bottom: initial;
      transition: box-shadow cubic-bezier(0.785, 0.135, 0.15, 0.86) .25s ;
      
    }

    .overlayCard:hover {
        background-color: white;
        box-shadow: 5px 5px 50px 5px white;
        cursor: pointer;
    }

    .overlayCard h4 {
        font-size: initial;
    }
    
    .overlayCard p {
        font-size: initial;
    }

  }